.footer {
    padding-top: 150px;
    padding-bottom: 40px;
    .footer__wrapper {
        display: grid;
        grid-template-columns: 6fr 5fr;
        gap: 115px;
        padding: 50px 0 52px;
        border-top: 1px solid $orange;
        border-bottom: 1px solid $orange;
    }
    .footer__left {

    }
    .footer__logo {
        img {
            width: 100%;
        }
        // margin-bottom: 40px;
        display: block;
    }
    .footer__logo-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
    }
    .footer__left-wrap {
        display: flex;
        justify-content: start;
        align-items: start;
        padding-top: 10px;
        border-top: 1px solid $orange;
    }
    .footer__reestr {
        max-width: 325px;
        width: 100%;
        flex-shrink: 0;
        padding: 8px 25px;
        box-sizing: border-box;
        border-radius: 10px;
        border: 1px solid var(--orange, #E35800);
        display: flex;
        justify-content: start;
        align-items: center;
        margin-left: 20px;
        img {
            margin-right: 31px;
            width: 59px;
            height: 59px;
        }
        span {
            color: $orange;
            line-height: 100%;
        }
    }
    .footer__nav {
        width: 100%;
    }
    .footer__nav-header {
        display: block;
        flex-shrink: 0;
        margin-right: 58px;
    }
    .footer__nav-wrap {
        display: grid;
        grid-template-columns: repeat(3, 120px);
        gap: 30px;

        justify-content: space-between;
    }
    .footer__nav-list {
        padding: 0;
        margin: 0;
        list-style-type: none;
        color: $black;
    }
    .footer__nav-item {
        line-height: 130%;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        a {
            font: inherit;
            color: inherit;
            display: block;
        }
    }

    .footer__right {
        padding-left: 20px;
        border-left: 1px solid $orange;
        color: $black;
    }
    .footer__info {
        display: grid;
        grid-template-columns: 2fr 3fr;
        gap: 35px;
        align-items: start;

        &.--socials {
            align-items: center;
        }
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }
    .footer__info-header {

    }
    .footer__info-wrap {

    }
    .footer__info-text {
        color: inherit;
        display: block;
        &:not(:last-child) {
            margin-bottom: 7px;
        }
    }
    .footer__info-link {
        color: $orange;
        display: block;
    }
    .footer__socials {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .footer__social {
        &:not(:last-child) {
            margin-right: 10px;
        }
    }

    .footer__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 30px;

        a {
            text-decoration: underline;
        }
    }
    .footer__bottom-text {
        opacity: 0.6;
        &:not(:first-child) {
            margin-left: 216px;
        }
    }
}

@media screen and (max-width: 1300px) {
  .footer .footer__wrapper {
    grid-template-columns: 1fr;
    // gap: 30px;
    gap: 0px;
    padding-top: 20px;
  }
  .footer .footer__right {
    border: none;
    padding-left: 0;
  }
  .footer {
    padding-top: 100px;
  }
  .footer .footer__logo {
    width: 100%;
  }
  .footer .footer__nav {
    display: none;
  }
  .footer .footer__info {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .footer .footer__logo-wrapper {
    flex-direction: column;
  }
  .footer .footer__left-wrap {
    display: none;
  }
  .footer .footer__reestr {
    margin-right: 0;
    max-width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    flex-direction: row;
    justify-content: center;
    margin-left: 0;
    margin-top: 20px;

    img {
        margin-bottom: 0;
        width: 44px;
        height: 44px;
        margin-right: 10px;
    }
    span {
        text-align: left;
        font-weight: 400;
        max-width: 153px;
    }
  }
  .footer .footer__bottom {
    flex-direction: column-reverse;
    align-items: start;
  }
  .footer .footer__bottom-text:not(:first-child) {
    margin-left: 0;
    display: block;
    margin-top: 10px;
  }
  .footer__bottom-wrap {
    margin-bottom: 10px;
  }
}