@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/new/Geometria-Light.ttf");
}
@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/new/Geometria-LightItalic.ttf");
}
  @font-face {
    font-style: normal;
    font-weight: 400;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-Regular.ttf");
  }
  @font-face {
    font-style: italic;
    font-weight: 400;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-Italic.ttf");
  }
  @font-face {
    font-style: normal;
    font-weight: 500;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-Medium.ttf");
  }
  @font-face {
    font-style: italic;
    font-weight: 500;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-MediumItalic.ttf");
  }
  @font-face {
    font-style: normal;
    font-weight: 700;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-Bold.ttf");
  }
  @font-face {
    font-style: italic;
    font-weight: 700;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/new/Geometria-BoldItalic.ttf");
  }