@mixin page-h2 {
  font-size: 55px;
  font-style: normal;
  line-height: 100%; /* 55px */
  @media screen and (max-width: 1400px) {
    font-size: 30px;
  }
}
@mixin page-text-40 {
  font-size: 40px;
  font-style: normal;
  line-height: 100%; /* 40px */
  @media screen and (max-width: 1400px) {
    font-size: 30px;
  }
}
@mixin page-text-30 {
  font-size: 30px;
  font-style: normal;
  line-height: 100%; /* 30px */
  @media screen and (max-width: 1400px) {
    font-size: 20px;
  }
}
@mixin page-text-25 {
  font-size: 25px;
  font-style: normal;
  @media screen and (max-width: 1400px) {
    font-size: 18px;
  }
}
@mixin page-text-22 {
  font-size: 22px;
  font-style: normal;
  @media screen and (max-width: 1400px) {
    font-size: 18px;
  }
}
@mixin page-text-20 {
  font-size: 20px;
  font-weight: 400;
  line-height: 120%; /* 24px */
  @media screen and (max-width: 1400px) {
    font-size: 18px;
  }
}
@mixin page-text-18 {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
}
@mixin page-link {
  font-size: 18px;
  font-style: normal;
  line-height: 120%; /* 24px */
  text-decoration-line: underline;
}

@mixin page-btn {
  display: inline-block;
  @include page-text-20;
  line-height: 100%;
  color: #fff;
  text-align: center;
  padding: 15px 78px;
  box-sizing: border-box;
  border-radius: 10px;
  background: var(--gradient, linear-gradient(87deg, #E35800 5.82%, #EC7A32 79.37%));
  border: none;
  outline: none;
  text-decoration: none;

  &.--white {
    background: $white;
    color: $orange;
  }
  &.--grey {
    background: $green;
    background: #7B7B7B;
    pointer-events: none;
  }
  &.--bordered {
    border: 1px solid $orange;
  }
  &.--dark-transparent {
    border-color: $black;
    color: $black;
  }

  @media screen and (max-width: 1500px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@mixin page-nav-btn {
  padding: 8px 36px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid var(--orange, #E35800);
  display: gird;
  grid-template-columns: repeat(2, auto);
  gap: 10px;
  justify-content: center;
  align-items: center;
  @include page-text-18;
  font-weight: 700;
  color: $orange;
  cursor: pointer;

  display: flex;
  justify-content: start;
  align-items: center;

  &.--prev img {
    transform: scale(-1, 1);
  }
  @media screen and (max-width: 1500px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}