.header {
   padding-top: 50px;
   padding-bottom: 20px;

  position: sticky;
  top: 0;
  z-index: 100;
  background: $beige;

.header__wrapper {
   display: flex;
   justify-content: start;
   align-items: center;
}   
.header__logo {
   max-width: 450px;
   width: 100%;
   flex-shrink: 0;

   img._mob {
     display: none;
   }
}
.header__wrap {
   padding: 18px 20px;
   border-radius: 20px;
   border: 1px solid var(--orange, #E35800);

   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 100%;
   margin-left: 20px;
}
.header__reestr {
   display: flex;
   justify-content: start;
   align-items: center;

   img {
       margin-right: 10px;
   }
}
.header__info {
   
}
.header__info-text {
   color: $orange;
   display: block;
}
.header__mobile {
  display: flex;
  justify-content: start;
  align-items: center;
  display: none;
}
.header__mobile-callback {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid $white;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  flex-shrink: 0;
}
.header__mobile-menu {
  padding: 10px 7px 10px;
  border: 1px solid $white;
  border-radius: 10px;
  height: 40px;
  width: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  flex-shrink: 0;
  cursor: pointer;

  svg {
     display: none;
  }
  &.--active {
     padding: 0;
     border: none;
     span {
        display: none;
     }
     svg {
        display: block;
        width: 100%;
        height: 100%;
        flex-shrink: 0;
     }
  }
}
}

@media screen and (max-width: 1365px) {
  .header .header__logo {
     flex-shrink: 10;
     img {
        display: none;
     }
     ._mob {
        display: block !important;
     }
  }
  .header .header__wrap {
     display: none;
  }
  .header {
     background: var(--gradient, linear-gradient(87deg, #E35800 5.82%, #EC7A32 79.37%));
     padding: 14px 0;
     position: sticky;
     top: 0;
     z-index: 100;
  }
  .header .header__wrapper {
     justify-content: space-between;
  }
  .header .header__mobile {
     display: flex;
  }

}