* {
  font-family: "Geometria";
  font-style: normal
}
html {
  // overflow-x: hidden;
}
body {
  padding-right: 0 !important;
  background-color: $beige;
  color: $black;
  overflow-x: hidden;
}
.page-h2 {
  @include page-h2;
}
.page-text-40 {
  @include page-text-40;
}
.page-text-30 {
  @include page-text-30;
}
.page-text-25 {
  @include page-text-25;
}
.page-text-22 {
  @include page-text-22;
}
.page-text-20 {
  @include page-text-20;
}
.page-text-18 {
  @include page-text-18;
}
.page-link {
  @include page-link;
}
.--text-bg {
  color: $white;
  background: $orange;
}
.page-btn {
  @include page-btn;
}
.page-nav-btn {
  @include page-nav-btn;
}
.--1-1 {
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}
.--1-2 {
  // grid-template-columns: 1fr 2fr;
  grid-template-columns: 8fr 11fr;
  gap: 21px 20px;
}
.--3-4 {
  // grid-template-columns: 3fr 4fr;
  grid-template-columns: 7fr 9fr;
  gap: 24px 24px;
}

.--bold {
  font-weight: 700;
}
.--italic {
  font-style: italic;
}
.--medium {
  font-weight: 500;
}
.--white-color {
  color: $white;
}
.--orange-color {
  color: $orange;
}
.--green-color {
  color: $green;
}
.--white-bg {
  background: $white !important;
}
.--orange-bg {
  background: $orange !important;
}
.--uppercase {
  text-transform: uppercase;
}

.bg {

  &-anti-white {
    background-color: $color-anti-flash-white;
  }
}


.content {
  section:not(:last-child) {
    margin-bottom: 150px;
  }
}

[data-accordion="content"] {
  max-height: 0;
  -webkit-transition: max-height .3s;
  -o-transition: max-height .3s;
  transition: max-height .3s;
  overflow: hidden;
}
[data-tabs="element"] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity .3s ease-in,visibility .3s ease-in;
  -o-transition: opacity .3s ease-in,visibility .3s ease-in;
  transition: opacity .3s ease-in,visibility .3s ease-in;  
}
[data-tabs="element"].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.breadcrumbs {
  padding-top: 0;
  .breadcrumbs__wrapper {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .breadcrumbs__item {
    color: $grey;
    display: flex;
    justify-content: start;
    align-items: center;

    &:not(:last-child) {
      &::after {
        content: '>';
        display: block;
        margin: 0 10px;
      }
    }
  }
}

.page__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}
.pagination {
  display: flex;
  justify-content: start;
  align-items: center;
  margin: 0 80px;

  .pagination__link {
    opacity: .5;
    &:not(:last-child) {
      margin-right: 10px;
    }
    &._active {
      opacity: 1;
    }
  }
}

.page-body {
  display: grid;
  grid-template-columns: 223px auto;
  gap: 17px;
}

.sidebar {

