.title {
  .title__wrapper {
    display: grid;
    grid-template-columns: 19fr 9fr;
    gap: 20px;
  }
  .title__main {
    width: 100%;
  }
  .title__slider {
    max-width: 900px;
    width: 100%;
  }
  .swiper-wrapper {

  }
  .title__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  .swiper-pagination {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    margin: 0 69px;

    .swiper-pagination-bullet {
      width: auto;
      height: auto;
      background: transparent;
      @include page-text-25;
      font-weight: 700;
      color: $orange;
      margin-left: 0;

      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  .title__slide {
    position: relative;
    border-radius: 20px; 
    overflow: hidden;
    border: 1px solid $orange;
    img {
      width: 100%;
      // height: 100%;
      object-fit: contain;

      &.--mob {
        display: none;
      }
    }
  }
  .title__header {
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;

    color: $orange;
    text-align: center;
    font-size: 70px;
    font-weight: 700;
    line-height: 100%; /* 70px */
    text-transform: uppercase;

    margin: 0;
    padding: 0;

    .--text-bg-el {
      // line-height: 140%;
      position: relative;
      color: #fff;
      display: inline-block;

      &::before {
        content: '';
        width: 102%;
        height: 66px;
        background: $orange;
        display: block;
        position: absolute;
        top: 52%;
        left: -5px;
        transform: translateY(-50%);
        z-index: -1;
      }
    }
  }
  .title__aside {

  }
  .title__aside-item {
    padding: 30px;
    box-sizing: border-box;
    border-radius: 20px;
    background: var(--orange, #E35800); 
    position: relative;
    min-height: 330px;

    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-direction: column;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  .title__aside-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &.--mob {
      display: none;
    }
  }
  .title__aside-hero {
    position: absolute;
    // top: 0;
    // right: -25px;
    top: -45px;
    right: -29px;
  }
  .title__aside-header {
    position: relative;
    z-index: 1;
    max-width: 260px;
    width: 100%;
    margin-bottom: 12px;
    display: block;
  }
  .title__aside-text {
    position: relative;
    max-width: 190px;
    width: 100%;
    z-index: 1;
    margin: 0;
  }
  .title__aside-link {
    display: block;
    position: relative;
    width: 100%;
    z-index: 1;
  }
}

