        display: none;
      }
    }
    button {
      border: none !important;
      background: transparent !important;
      @include page-text-18;
      color: $black !important;
      overflow: visible;
      cursor: pointer;

      &::after {
        background-image: url('../img/svg/select-arrow.svg');
