.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    max-width: 670px;
    width: 100%;
    padding: 50px 50px;
    border: 1px solid $orange;
    border-radius: 20px;

    background-color: $color-default-white;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: -15px;
    right: -15px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;

    border: 1px solid $orange;
    border-radius: 10px;
    background: $white;
    color: $orange;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.mymodal-wrapper {
  display: flex;
  flex-direction: column;
}
.mymodal__headline {
  display: flex;
  flex-direction: column;
}
.mymodal__header {
  display: block;
  margin-bottom: 30px;
  text-align: center;
  line-height: 120%;
}
.mymodal__btn {
  margin: 0 auto;
  cursor: pointer;
}
.mymodal__form {
  max-width: 390px;
  width: 100%;
  margin: 0 auto;

  .request__terms-text {
    a {
      color: $black;
    }
  }
}
.mymodal__form-bottom {

}
.mymodal__submit {
  width: 100%;
  margin-top: 20px;
}